            /* navbar */
            
            .nav-bar {
                padding: 25px 20px 18px;
                .nav {
                    justify-content: space-between;
                    flex-wrap: nowrap;
                    @extend %flex;
                    position: relative;
                    .logo {
                        @include mob425 {
                            width: 200px;
                        }
                        img {
                            width: 100%;
                        }
                    }
                    .slide-bar {
                        @extend %flex;
                        justify-content: space-between;
                        @extend %trans;
                        @extend %position;
                        @include Sm {
                            position: fixed;
                            width: 100%;
                            height: 100%;
                            background: $white-color;
                            z-index: 99999;
                            transform-origin: right;
                            transform: scaleX(0);
                            &.open {
                                transform-origin: left;
                                transform: scaleX(1);
                            }
                        }
                        nav {
                            overflow: hidden;
                            @extend %flex;
                            margin: 0;
                            @extend %trans;
                            ul {
                                @extend %flex;
                                margin: 0;
                                @extend %trans;
                                @include Sm {
                                    display: block;
                                    position: absolute;
                                    left: 50%;
                                    top: 50%;
                                    transform: translate(-50%, -50%);
                                }
                            }
                            li {
                                margin-left: 20px;
                                text-align: center;
                                @include Md {
                                    margin-left: 15px;
                                }
                                @include Sm {
                                    margin-bottom: 15px;
                                }
                                a {
                                    color: $black-color;
                                    font-size: 16px;
                                    @extend %trans;
                                    font-family: "frtBold";
                                    @include Md {
                                        font-size: 14px;
                                    }
                                    &:hover {
                                        color: $main-color;
                                    }
                                }
                            }
                            &.fadein ul {
                                opacity: 0;
                                @include Sm {
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }
                .close-tab {
                    position: absolute;
                    left: 30px;
                    top: 30px;
                    display: none;
                    @include Sm {
                        display: block;
                    }
                    i.fa-times {
                        color: $white-color;
                        background-color: $main-color;
                        width: 52px;
                        height: 52px;
                        line-height: 52px;
                        text-align: center;
                        cursor: pointer;
                    }
                }
                /* search */
                .search {
                    @extend %flex;
                    justify-content: center;
                    flex-direction: row-reverse;
                    overflow: hidden;
                    @include Sm {
                        position: absolute;
                        right: 30px;
                        top: 30px;
                        z-index: 999999;
                    }
                    &.media {
                        display: none;
                    }
                    @include Sm {
                        &.large {
                            display: none;
                        }
                        &.media {
                            display: block;
                            position: absolute;
                            right: 30px;
                            top: 30px;
                            z-index: 999999;
                        }
                    }
                    .btn-search {
                        width: 52px;
                        height: 52px;
                        line-height: 52px;
                        text-align: center;
                        background: $main-color;
                        color: $white-color;
                        display: inline-table;
                        cursor: pointer;
                        &.fadeout {
                            display: none;
                            @include Sm {
                                display: block;
                            }
                        }
                    }
                    form {
                        @extend %flex;
                        justify-content: center;
                        position: relative;
                        left: -100%;
                        overflow: hidden;
                        @extend %trans;
                        @extend %dimen;
                        @include Sm {
                            position: fixed;
                            left: 0;
                            top: 0;
                            visibility: hidden;
                            opacity: 0;
                            background: #333333e6;
                            &.open {
                                visibility: visible;
                                opacity: 1;
                            }
                        }
                        &.open {
                            left: 0;
                        }
                        input {
                            width: 0;
                            height: 52px;
                            background: transparent;
                            background: $white;
                            line-height: 52px;
                            @extend %trans;
                            border-radius: 20px;
                            &.input-open {
                                width: 250px;
                                position: relative;
                                border-radius: 29px;
                                padding: 0px 10px;
                            }
                        }
                        button {
                            position: relative;
                            left: 40px;
                            display: none;
                            width: 30px;
                            height: 30px;
                            line-height: 30px;
                            text-align: center;
                            background: $main-color;
                            color: $white-color;
                            border-radius: 50%;
                            padding: 0;
                            @extend %trans;
                            i {
                                font-size: 12px;
                            }
                            &.btn-open {
                                display: block;
                            }
                        }
                        .fa.fa-times {
                            position: absolute;
                            top: 20%;
                            font-size: 14px;
                            color: $white;
                            width: 30px;
                            height: 30px;
                            line-height: 30px;
                            text-align: center;
                            background: $main-color;
                            border-radius: 50%;
                            cursor: pointer;
                        }
                    }
                }
                // toggle
                .toggle {
                    display: none;
                    cursor: pointer;
                    @include Sm {
                        display: block;
                    }
                    i {
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-50%);
                        font-size: 25px;
                    }
                }
            }
            
            // navbar
            // latest-news
            .latest-news {
                background-color: $black-color;
                height: 280px;
                background-image: url(../images/Layer-2.png);
                background-size: auto;
                background-repeat: no-repeat;
                position: relative;
                z-index: 9999;
                @include Sm {
                    height: 250px;
                    overflow: hidden;
                }
                @include Xs {
                    height: 350px;
                }
                .content {
                    justify-content: center;
                    @extend %flex;
                    height: 280px;
                    @include Sm {
                        height: 250px;
                    }
                    @include Xs {
                        height: 350px;
                    }
                    .cont {
                        position: relative;
                        z-index: 1;
                        h1 {
                            font-size: 20px;
                            color: $white-color;
                            margin-bottom: 30px;
                            font-family: "frtBlk";
                            line-height: 1.7;
                            @include Sm {
                                margin: 15px 0px;
                            }
                            @include Xs {
                                font-size: 17px;
                            }
                        }
                    }
                    .search-box {
                        @extend %flex;
                        justify-content: space-between;
                        @include mob480 {
                            display: block;
                        }
                        form {
                            @extend %flex;
                            margin: 5px;
                            flex-flow: wrap;
                            @include Xs {
                                justify-content: center;
                            }
                            @include mob480 {
                                display: block;
                                text-align: center;
                            }
                        }
                        .bootstrap-select .dropdown-toggle .filter-option-inner-inner {
                            font-size: 16px;
                            color: $par!important;
                            font-family: "frt";
                            @extend %border;
                            line-height: 34px;
                            padding-right: 10px;
                            text-align: right;
                            @include mob480 {
                                text-align: center;
                            }
                        }
                        .dropdown.bootstrap-select {
                            width: 240px;
                            height: 46px;
                            margin-left: 5px;
                            background-color: $white-color;
                            @include Md {
                                width: 200px;
                            }
                            @include Sm {
                                margin: 5px;
                                width: 215px
                            }
                            @include Xs {
                                width: 200px;
                                height: 42px
                            }
                            @include mob480 {
                                display: block;
                                margin: 5px auto;
                                width: 80%;
                            }
                        }
                        .dropdown-toggle::after {
                            content: "\f107";
                            @extend %icon;
                            color: $par!important;
                            @extend %dimen;
                            text-align: left;
                            line-height: 32px;
                            margin-left: 0;
                            border-left: 0;
                            font-size: 12px;
                            @include mob480 {
                                width: auto;
                            }
                        }
                        .dropdown.bootstrap-select.show .dropdown-toggle::after {
                            content: "\f106";
                            @extend %icon;
                            color: $par!important;
                        }
                        .bootstrap-select .dropdown-toggle:focus,
                        .bootstrap-select>select.mobile-device:focus+.dropdown-toggle,
                        button.btn.dropdown-toggle.btn-light {
                            background-color: $white-color;
                            @extend %border;
                        }
                        div#bs-select-2,
                        div#bs-select-1,
                        div#bs-select-3,
                        div#bs-select-4 {
                            margin-left: 12px;
                        }
                        .dropdown-menu.show {
                            max-height: 200px!important;
                        }
                        .bootstrap-select>.dropdown-toggle {
                            height: 100%;
                        }
                        .bootstrap-select .dropdown-menu li a {
                            font-size: 14px;
                            font-family: "frt";
                            @extend %border;
                        }
                        button[type=submit] {
                            width: 100px;
                            height: 46px;
                            background: $main-color;
                            color: $white;
                            @include Xs {
                                height: 42px;
                                margin-top: 10px;
                            }
                            @include mob480 {
                                text-align: center;
                                width: 80%;
                            }
                            i {
                                font-size: 12px;
                            }
                            span {
                                font-size: 18px;
                                font-family: "frtBold";
                                margin-right: 6px;
                            }
                        }
                        .content li.active,
                        .dropdown-item.active {
                            color: $main-color;
                            @extend %border;
                            margin: 0;
                        }
                        button.btn.dropdown-toggle.btn-light {
                            margin: 0;
                        }
                        .dropdown-item.active,
                        .dropdown-item:active {
                            background-color: $white-color;
                        }
                        .bootstrap-select .dropdown-toggle:focus,
                        .bootstrap-select .dropdown-menu li:focus,
                        .bootstrap-select>select.mobile-device:focus+.dropdown-toggle,
                        .bootstrap-select .dropdown-menu li:active,
                        button.btn.dropdown-toggle.btn-light:focus,
                        .show>.btn-light.dropdown-toggle:focus {
                            outline: none!important;
                            box-shadow: none;
                            @extend %border;
                        }
                    }
                }
            }
            
            //latest news
            //- content
            .world-cars {
                padding: 50px 0px;
