// color
$main-color:#ed2228;
$white-color:#f6f6f6;
$black-color:#000;
$white-black:#f9f9f9;
$p:#7d7d7d;
$par :#adadad;
$btn-one:#efa81f;
$btn-two:#7d7d7d;
$btn-three:#bebfbe;
$white:#fff;
$border:#f8f8f8;
// flex
%flex {
    display: flex;
    align-items: center;
}

section,
footer {
    overflow: hidden;
}

// trans
%trans {
    transition: all .7s ease-in-out;
}

// BUTTON
button,
a:link,
button:active,
button:focus,
input,
input:focus {
    border: none;
    outline: none;
    background-color: transparent;
}

select,
select:focus {
    outline: none;
}

a:link,
button {
    cursor: pointer;
    text-decoration: none;
}

// border
%border {
    border: none;
    outline: none;
}

// icon
%icon {
    font-family: "Font Awesome 5 free";
    font-weight: 900;
    border-top: 0;
    margin-top: 0;
    border-bottom: 0;
}

// dimen{
%dimen {
    width: 100%;
    height: 100%;
}

// pos
%position {
    left: 0;
    top: 0;
}

// scroll
::-webkit-scrollbar-thumb {
    background-color: #c3c3c3;
}

 ::-webkit-scrollbar {
    width: 5px;
}

 ::-webkit-scrollbar-track {
    background-color: #e5e5e5;
}

// button
a.btn-bottom,
a.btn-bottom:focus {
    background-color: $main-color!important;
    color: $white;
    font-family: "frtBold";
    display: block;
    height: 52px;
    line-height: 52px;
    text-align: center;
    width: 100%;
    font-size: 14px;
}

// img
.img {
    height: 280px;
}

.img-video-1 {
    background-image: url(../images/Layer-4.png);
}

.img-video-2 {
    background-image: url(../images/Layer-690.png);
}

.img-video-3 {
    background-image: url(../images/Layer-691.png);
}